import React, {Component} from 'react';
import './App.css';
import MapGL from 'react-map-gl';
import Typography from '@material-ui/core/Typography';
import Paper from '@material-ui/core/Paper';
import Dashboard from '@material-ui/icons/Dashboard';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import Fab from '@material-ui/core/Fab';
import {ValidatorForm, TextValidator} from 'react-material-ui-form-validator';

const MAP_STYLE_URL = 'styles/basic.json'
const NODE_INFO_URL = 'node-info.json'
const MAPBOX_TOKEN = 'pk.eyJ1IjoiaHlwZXJpb25sYWIiLCJhIjoiY2pwYXNvbTJiMm90ZjNxcDd3Y3kxNGtmYiJ9.s-np__x-B_MgHvp06NxV5w'
const DISCUSS_URL = 'https://talk.hyn.space/'

class App extends Component {
  state = {
    viewport: {
      latitude: 22.305110,
      longitude: 114.188488,
      zoom: 10,
      width: "100vw",
      height: "100vh",
    },
    style: '',
    host: '',
    contact: '',
    open: false,
    committing: false,
    nodeName: '',
      contractAddr: ''
  };

    componentWillMount() {
        let host = window.location.host;
        let tileServer = document.location.protocol + "//" + host + "/api/v1/hyn/map3/{z}/{x}/{y}.pbf";
        this.setState({host: host});
        fetch(`https://api.hyn.space/api/v1/tile/feedback/check?ip=${host}`)
            .then(res => res.json())
            .then(data => {
                console.log(data)
                if (data.data === false) {
                    this.setState({open: true})
                }
            });

      fetch(NODE_INFO_URL)
          .then(res => res.json())
          .then(nodeInfo => {
            this.setState({nodeName: nodeInfo.nodeName});
            this.setState({contractAddr: nodeInfo.contractAddr});
          });

      fetch(MAP_STYLE_URL)
            .then(res2 => res2.json())
            .then(style => {
                style.sources.composite.tiles.push(tileServer);
                return style
            })
            .then(style => {
                this.setState({style})
            })
            .catch(e => {
                console.warn(e)
            })
    }

  componentDidMount() {
    this._updateCurrentLocation()
    // setTimeout(() => {
    //   console.log(this.mapRef.getMap())
    // }, 2000)
  }

  _onViewportChange = viewport => {
    viewport['width'] = "100vw";
    viewport['height'] = "100vh";
    this.setState({viewport})
  };

  _updateCurrentLocation = () => {
    navigator.geolocation.getCurrentPosition(position => {
      const viewport = {...this.state.viewport};
      viewport.longitude = position.coords.longitude;
      viewport.latitude = position.coords.latitude;
      this.setState({viewport})
      console.log('get location', position)
    }, (error) => {
      console.warn(error)
    })
  };

  handleClickOpen = () => {
    this.setState({open: true});
  };

  handleClose = () => {
    this.setState({open: false});
  };

  handleSubmit = (e) => {
    e.preventDefault()
    if (this.state.host) {
      console.log('email', this.state.contact)
      this.setState({committing: true})
      fetch("https://api.hyn.space/api/v1/tile/feedback", {
        method: 'POST',
        headers: new Headers({
          'Content-Type': 'application/x-www-form-urlencoded', // <-- Specifying the Content-Type
        }),
        body: `ip=${this.state.host}&contact=${this.state.contact}` // <-- Post parameters
      })
        .then(res => res.json())
        .then(res => {
          this.setState({committing: false, open: false})
        })
    }
  };

  onContactInputChange = (event) => {
    this.setState({contact: event.target.value, contactError: false})
  };

  render() {
    let {viewport, style, host, nodeName, contractAddr} = this.state;
    let url = "";
    if (nodeName === "" || nodeName === undefined) {
      nodeName = host
    } else {
      url = "https://etherscan.io/address/" + contractAddr;
    }

    return (
      <div className="App">
        <MapGL
          ref={map => this.mapRef = map}
          {...viewport}
          mapStyle={style}
          onViewportChange={this._onViewportChange}
          mapOptions={{
            localIdeographFontFamily: "'Noto Sans', 'Noto Sans CJK SC', sans-serif",
            attributionControl: false
          }}
          mapboxApiAccessToken={MAPBOX_TOKEN}
        >
        </MapGL>
        <div className={'App-Container'}>
          <div className="App-Top-Nav">
            <span className="circle"/>
            <div style={{paddingLeft: '8px'}}>
              Node: {nodeName} { url !== "" &&
                <a href={url} rel="noopener noreferrer" target='_blank'
                 className='node'>
                <span style={{marginLeft: '8px'}}>View Contract</span>
              </a>}
            </div>
            <div style={{flex: 1}}/>
            <a href={`http://${host}:3000`} rel="noopener noreferrer" target='_blank'
               className='dashboard'>
              <Dashboard>Dashboard</Dashboard>
              <div style={{marginLeft: '8px'}}>DASHBOARD</div>
            </a>
          </div>
          <Paper elevation={1} className="App-Guide" square>
            <Typography>Tips: This map is to test if your service node works normally. If you see
              this map, it means your node is providing map services. </Typography>
          </Paper>

          <div className="fab-help">
            <Fab href={DISCUSS_URL} target="_blank" rel="noopener noreferrer" color={'secondary'}
                 size="large">
              Forum
            </Fab>
          </div>

          <Dialog
            open={this.state.open}
            onClose={this.handleClose}
            disableBackdropClick
            disableEscapeKeyDown
            aria-labelledby="form-dialog-title"
          >
            <ValidatorForm onSubmit={this.handleSubmit} onError={errors => console.warn(errors)}>
              <DialogTitle id="form-dialog-title">Support Us</DialogTitle>
              <DialogContent>
                <DialogContentText>
                  Dear friend, welcome to join Hyperion Map3 network, and become an important member
                  of Hyperion community. Please leave your contact information below to help us
                  improve our service.
                </DialogContentText>
                <TextValidator
                  autoFocus
                  margin="dense"
                  id="name"
                  name="contact"
                  value={this.state.contact}
                  label="Email or Phone Number"
                  fullWidth
                  validators={['required']}
                  errorMessages={['* this field is required']}
                  onChange={this.onContactInputChange}
                  disabled={this.state.committing}
                />

                <div className="link-discuss">
                  <a href={DISCUSS_URL} target="_blank" rel="noopener noreferrer">
                    Come share your ideas in Hyperion Forum!
                  </a>
                </div>

              </DialogContent>
              <DialogActions>
                <Button onClick={this.handleClose} color="primary" disabled={this.state.committing}>
                  Cancel
                </Button>
                <Button type="submit" disabled={this.state.committing} color="primary">
                  Submit
                </Button>
              </DialogActions>
            </ValidatorForm>
          </Dialog>
        </div>
      </div>
    );
  }
}

export default App;
